import { createBrowserRouter } from 'react-router-dom'
import About from './About'
import Home from './Home'
import Menu from './Menu'
import Orders from './Orders'

export const router = createBrowserRouter([
  {
    path: '/',
    element: <Home />,
  },
  {
    path: '/about',
    element: <About />,
  },
  {
    path: '/menu',
    element: <Menu />,
  },
  {
    path: '/orders',
    element: <Orders />,
  },
])
