import Logo from 'assets/logo.jpg'
import './styles.scss'

const Orders = () => {
  return (
    <div className='orders-container'>
      <h1 className='orders-container__title'>Coming Soon</h1>
      <a href='/'>
        <img className='orders-container__logo' src={Logo} />
      </a>
    </div>
  )
}

export default Orders
