import Logo from 'assets/logo.jpg'
import './styles.scss'

const Home = () => {
  return (
    <div className='home-container'>
      <img className='home-container__logo' src={Logo} />
      <div className='home-container__link-section'>
        <a className='home-container__link-button' href='/about'>
          Conoce quiénes somos
        </a>
        <a className='home-container__link-button' href='/menu'>
          Menú
        </a>
        <a className='home-container__link-button' href='/orders'>
          Pedidos
        </a>
      </div>
    </div>
  )
}

export default Home
