import './styles.scss'
import Logo from 'assets/logo.jpg'
import menuData from './menu.json'
import { useState } from 'react'
import ImageSearchIcon from '@mui/icons-material/ImageSearch'
import { Modal } from '@mui/material'
import largeBerryImg from 'assets/fresas-con-crema-grande.jpg'
import smallBerryImg from 'assets/fresas-con-crema-sin-topping-pequeño.jpg'

const loadedMenu = JSON.parse(JSON.stringify(menuData))

const Menu = () => {
  const [open, setOpen] = useState(false)
  const [selectedItem, setSelectedItem] = useState<any>(null)
  const handleOpen = (item: any) => {
    setSelectedItem(item)
    setOpen(true)
  }
  const handleClose = () => setOpen(false)

  return (
    <div className='menu-container'>
      <a href='/'>
        <img className='menu-container__logo' src={Logo} />
      </a>

      <h1 className='menu-container__title'>Menú</h1>

      <div className='menu-container__menu-table'>
        {loadedMenu.menu.map((item: any) => (
          <>
            <div className='menu-container__ribbon' key={item.title}>
              {item.title}
            </div>
            {item.itemList.map((subItem: any) => (
              <div className='menu-container__subitems-container' key={subItem.name}>
                <span className='menu-container__subitem-title'>{subItem.name}</span>
                <div className='menu-container__divider'></div>
                <span className='menu-container__prices-text'>
                  {subItem.prices.length === 1
                    ? subItem.prices[0].value
                    : `${subItem.prices[0].value} / ${subItem.prices[1].value}`}
                </span>
                <button className='menu-container__button' onClick={() => handleOpen(subItem)}>
                  <ImageSearchIcon />
                </button>
              </div>
            ))}
          </>
        ))}
      </div>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby='modal-modal-title'
        aria-describedby='modal-modal-description'
      >
        <div className='modal__body'>
          {/* {JSON.stringify(selectedItem)} */}
          {selectedItem && (
            <>
              <span className='modal__title'>{selectedItem.name}</span>
              <div className='modal__image-container'>
                <div className='modal__image-item'>
                  <span>Grande (x onz)</span>
                  <img src={largeBerryImg} />
                </div>
                <div className='modal__image-item'>
                  <span>Pequeño (y onz)</span>
                  <img src={smallBerryImg} />
                </div>
              </div>
            </>
          )}
        </div>
      </Modal>
    </div>
  )
}

export default Menu
