import { createTheme } from '@mui/material'

declare module '@mui/material/styles' {
  interface Theme {
    customPalette: {
      white: string
      paleSilver: string
      lightCoral: string
      mantis: string
      mimiPink: string
    }
  }
  // allow configuration using `createTheme`
  interface ThemeOptions {
    customPalette?: {
      white?: string
      paleSilver?: string
      lightCoral?: string
      mantis?: string
      mimiPink?: string
    }
  }
}

export const theme = createTheme({
  customPalette: {
    white: '#FEFEFE',
    paleSilver: '#DEC9C4',
    lightCoral: '#FC7F7D',
    mantis: '#80D071',
    mimiPink: '#F9CEDF',
  },
})
